.global-field-style,
.link-field-wrapper {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style,
  .link-field-wrapper {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style,
  .link-field-wrapper {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style,
  .link-field-wrapper {
    width: 95%;
  }
}
.link-field-wrapper {
  border-radius: 3px;
}
.link-field-wrapper:hover {
  background: #FAFAF8;
}
.link-field-wrapper.different-value {
  background: #FCFAE2;
}
.link-field-wrapper.deleted-value {
  opacity: 0.5;
}
.link-field-wrapper .label {
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  display: inline-block;
  text-align: left;
  min-width: 110px;
  max-width: 110px;
  overflow: hidden;
}
.link-field-wrapper .label .required-icon {
  margin-left: 2px;
  color: red;
}
.link-field-wrapper .label .aspect-not-aligned,
.link-field-wrapper .label .single-value-warning {
  margin-left: 8px;
  color: #999;
  font-size: 16px;
}
.link-field-wrapper .label .importance-level {
  font-size: 12px;
  font-family: helvetica;
  font-weight: 300;
}
.link-field-wrapper .label .importance-level > span {
  margin-left: 2px;
}
.link-field-wrapper .input-wrapper .input-inner-wrapper {
  display: flex;
}
.link-field-wrapper .input-wrapper .ant-select {
  min-width: 650px;
}
.link-field-wrapper .input-wrapper .actions-wrapper {
  display: inline-block;
}
.link-field-wrapper .input-wrapper .actions-wrapper button {
  margin-left: 5px;
  border: 0;
}
