.global-field-style,
.image-list-wrapper {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style,
  .image-list-wrapper {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style,
  .image-list-wrapper {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style,
  .image-list-wrapper {
    width: 95%;
  }
}
.image-list-wrapper {
  border-radius: 3px;
}
.image-list-wrapper .img-list-container {
  max-width: 95%;
}
.image-list-wrapper .img-list-container .noPrimaryImage {
  width: 105px;
  height: 105px;
}
.image-list-wrapper .img-list-container .noImageContainer {
  padding: 5px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
}
.image-list-wrapper:hover {
  background: #fafaf8;
}
.image-list-wrapper.different-value {
  background: #fcfae2;
}
.image-list-wrapper.changed-value {
  border: 1px solid #ffcc0c;
}
.image-list-wrapper .label {
  display: inline-block;
  text-align: left;
  min-width: 110px;
}
.image-list-wrapper .img-item {
  display: inline-block;
  padding: 5px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
}
.image-list-wrapper .img-item:hover {
  cursor: zoom-in;
}
.image-list-wrapper .button-wrapper {
  flex-flow: column;
  display: flex;
  justify-content: space-evenly;
}
