.product-info-card-wrapper {
  width: 208px;
}
.product-info-card-wrapper .ant-carousel {
  line-height: 0;
}
.product-info-card-wrapper .product-info-card-img {
  width: 100%;
  height: 194px;
  border: solid 1px #d9d9d9;
  border-bottom: none;
}
.product-info-card-wrapper .product-info-container {
  border: solid 1px #dcdcd4;
  margin-bottom: 10px;
}
.product-info-card-wrapper .product-info-container .product-info {
  text-align: left;
  font-size: 12px;
  margin-top: 8px;
  margin-left: 8px;
}
.product-info-card-wrapper .product-info-container .product-info label {
  font-weight: 700;
}
.product-info-card-wrapper .product-info-container .product-info .attributeValue {
  margin-bottom: 10px;
}
