.duplicates-wrapper .title-section {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin: 20px 30px;
}
.duplicates-wrapper .title-section .edit-mode {
  font-weight: 700;
  font-size: 16px;
}
.duplicates-wrapper .side-by-side-wrapper {
  display: flex;
  justify-content: center;
}
.duplicates-wrapper .product-wrapper {
  border: 1px solid #ddd;
  border-radius: 3px;
  padding: 25px;
  position: relative;
  margin: 20px 30px;
  width: 45%;
  padding-bottom: 53px;
}
.duplicates-wrapper .product-wrapper .attributes-read-only-section label {
  min-width: 210px;
  max-width: 210px;
  padding-right: 10px;
  overflow-wrap: break-word;
}
.duplicates-wrapper .product-wrapper .deleted-product {
  background: #e7e7e7;
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.7;
  line-height: 90px;
  font-weight: 700;
  z-index: 120;
  padding-top: 25%;
  font-size: 40px;
  left: 0;
  top: 0;
  z-index: 200;
}
.duplicates-wrapper .product-wrapper .ant-divider-horizontal {
  margin: 40px 0;
}
.duplicates-wrapper .product-wrapper .item-surviving-title-wrapper {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.duplicates-wrapper .product-wrapper .item-surviving-title {
  position: absolute;
  top: -11px;
  border: 1px solid #ddd;
  padding: 2px 15px;
  border-radius: 3px;
  background: #fff;
  z-index: 201;
}
.duplicates-wrapper .product-wrapper .item-surviving-title > span {
  font-size: 11px;
}
.duplicates-wrapper .actions-section {
  border-top: 1px solid #ddd;
  height: 32px;
  padding-top: 12px;
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 20px;
  width: 94%;
  z-index: 201;
}
.duplicates-wrapper .actions-section .delete-left-side {
  position: absolute;
  right: 0;
}
.duplicates-wrapper .actions-section .toggle-btn {
  display: inline-block;
  margin-right: 30px;
}
.duplicates-wrapper .actions-section .toggle-btn label {
  font-family: helvetica;
  font-weight: 100;
  margin-left: 10px;
}
.duplicates-wrapper .add-aspect-button-wrapper {
  position: relative;
  right: 16%;
}
.duplicates-wrapper .add-aspect-button-wrapper > button {
  position: absolute;
  right: 0;
  top: -60px;
  color: #1890ff;
}
.duplicates-wrapper .submit-wrapper {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 20px;
  margin-right: 3%;
}
.duplicates-wrapper .submit-wrapper button {
  width: 100px;
}
