.global-field-style {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style {
    width: 95%;
  }
}
