.reason-popover-content {
  width: 460px;
}
.reason-popover-content .additional-info-wrapper,
.reason-popover-content .additional-urls-wrapper {
  border: 1px solid #DDD;
  border-radius: 3px;
  padding: 10px 10px 0px 10px;
  max-height: 140px;
  overflow: auto;
}
.reason-popover-content .url-item {
  display: flex;
  margin-bottom: 10px;
}
.reason-popover-content .url-item label {
  width: 200px;
  margin-bottom: 10px;
}
.reason-popover-content section.action-btn {
  display: flex;
  flex-flow: row-reverse;
  margin-top: 15px;
}
.reason-popover-content section.action-btn button {
  margin-left: 10px;
  width: 80px;
}
.reason-popover-content .mandatory {
  color: red;
}
.reason-popover-content .add-url {
  margin-left: 10px;
  color: #1890ff;
}
.reason-popover-content .add-url:hover {
  cursor: pointer;
}
