.no-ums-input-wrapper {
  display: flex;
  flex-wrap: wrap;
  min-height: 32px;
  flex-direction: row;
  color: rgba(0, 0, 0, 0.65);
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  outline: none;
}
.no-ums-input-wrapper .selected-item:hover {
  cursor: pointer;
}
.no-ums-input-wrapper .selected-item .text-wrapper {
  overflow: hidden;
  max-width: 230px;
  text-overflow: ellipsis;
  display: inline-block;
}
.no-ums-input-wrapper:hover,
.no-ums-input-wrapper:active {
  border-color: #40a9ff;
}
.no-ums-input-wrapper label.values-wrapper {
  width: 100%;
}
.no-ums-input-wrapper .values-wrapper {
  text-align: left;
}
.no-ums-input-wrapper .values-wrapper > span {
  display: inline-block;
  height: 24px;
  vertical-align: middle;
  white-space: pre;
  margin: 4px 2px 5px 2px;
  padding: 1px 5px 3px 10px;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa;
  border: 1px solid #e8e8e8;
  border-radius: 2px;
  cursor: default;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.no-ums-input-wrapper .values-wrapper > span:first-child {
  margin-left: 5px;
}
.no-ums-input-wrapper .values-wrapper > span > svg {
  display: inline-block;
  color: rgba(0, 0, 0, 0.45);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  padding-left: 5px;
  padding-bottom: 3px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.no-ums-input-wrapper .values-wrapper > span > svg:hover {
  color: rgba(0, 0, 0, 0.75);
}
.no-ums-input-wrapper .values-wrapper > span .copy-icon,
.no-ums-input-wrapper .values-wrapper > span .delete-icon {
  margin-right: 5px;
  vertical-align: top;
}
.no-ums-input-wrapper input {
  border: none;
  outline: none;
  height: 25px;
  margin-top: 3px;
  margin-left: 5px;
}
