.controlled-select-wrapper {
  position: relative;
}
.controlled-select-wrapper form {
  display: flex;
  flex-wrap: wrap;
  min-height: 32px;
  flex-direction: row;
  color: rgba(0, 0, 0, 0.65);
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  outline: none;
}
.controlled-select-wrapper form:hover,
.controlled-select-wrapper form:active {
  border-color: #40a9ff;
  background: #fff;
}
.controlled-select-wrapper form .values-wrapper {
  text-align: left;
  width: 100%;
}
.controlled-select-wrapper form .values-wrapper > .selected-item {
  display: inline-block;
  height: 24px;
  max-width: 320px;
  vertical-align: middle;
  margin: 4px 2px 5px 2px;
  padding: 1px 5px 3px 10px;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa;
  border: 1px solid #e8e8e8;
  border-radius: 2px;
  cursor: default;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin-left: 5px;
}
.controlled-select-wrapper form .values-wrapper > .selected-item .info-circle-wrapper {
  line-height: 18px;
  display: inline-block;
}
.controlled-select-wrapper form .values-wrapper > .selected-item .info-circle-wrapper > i {
  vertical-align: baseline;
  vertical-align: initial;
  margin-right: 4px;
}
.controlled-select-wrapper form .values-wrapper > .selected-item .text-wrapper {
  white-space: pre;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 230px;
  display: inline-block;
}
.controlled-select-wrapper form .values-wrapper > .selected-item:hover {
  cursor: pointer;
}
.controlled-select-wrapper form .values-wrapper > .selected-item > svg {
  display: inline-block;
  color: rgba(0, 0, 0, 0.45);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  padding-left: 5px;
  padding-bottom: 3px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.controlled-select-wrapper form .values-wrapper > .selected-item > svg:hover {
  color: rgba(0, 0, 0, 0.75);
}
.controlled-select-wrapper form .values-wrapper > .selected-item .copy-icon,
.controlled-select-wrapper form .values-wrapper > .selected-item .delete-icon {
  margin-right: 5px;
  vertical-align: top;
  color: rgba(0, 0, 0, 0.45);
}
.controlled-select-wrapper form .values-wrapper .down-arrow {
  float: right;
  margin-top: 8px;
  margin-right: 8px;
}
.controlled-select-wrapper form input {
  border: none;
  outline: none;
  height: 25px;
  margin-top: 3px;
  margin-left: 5px;
}
.controlled-select-wrapper .options-wrapper {
  list-style-type: none;
  padding-left: 0;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin-top: 1px;
  position: absolute;
  width: 100%;
}
.controlled-select-wrapper .options-wrapper.hide {
  display: none;
}
.controlled-select-wrapper .options-wrapper.show {
  display: inherit;
  z-index: 100000;
  max-height: 150px;
  overflow: auto;
}
.controlled-select-wrapper .options-wrapper > li {
  text-align: left;
  padding: 2px 10px;
  cursor: pointer;
}
.controlled-select-wrapper .options-wrapper > li:hover {
  background: #fafafa;
}
