.global-field-style,
.regular-field-wrapper {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style,
  .regular-field-wrapper {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style,
  .regular-field-wrapper {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style,
  .regular-field-wrapper {
    width: 95%;
  }
}
.regular-field-wrapper {
  border-radius: 3px;
  margin-bottom: 10px;
}
.regular-field-wrapper .field-value {
  max-width: 800px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
}
.regular-field-wrapper:hover {
  background: #FAFAF8;
}
.regular-field-wrapper.different-value {
  background: #FCFAE2;
}
.regular-field-wrapper.deleted-value {
  opacity: 0.5;
}
.regular-field-wrapper .label {
  display: inline-block;
  text-align: left;
  min-width: 110px;
  max-width: 110px;
  overflow: hidden;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
}
.regular-field-wrapper .label .required-icon {
  margin-left: 2px;
  color: red;
}
.regular-field-wrapper .label .aspect-not-aligned,
.regular-field-wrapper .label .single-value-warning {
  margin-left: 8px;
  color: #999;
  font-size: 16px;
}
.regular-field-wrapper .label .importance-level {
  font-size: 12px;
  font-family: helvetica;
  font-weight: 300;
}
.regular-field-wrapper .label .importance-level > span {
  margin-left: 2px;
}
.regular-field-wrapper .input-wrapper .input-inner-wrapper {
  display: flex;
}
.regular-field-wrapper .input-wrapper .ant-select {
  min-width: 650px;
}
.regular-field-wrapper .input-wrapper .actions-wrapper {
  display: inline-block;
}
.regular-field-wrapper .input-wrapper .actions-wrapper button {
  margin-left: 5px;
  border: 0;
}
.aspect-title-wrapper {
  text-align: left;
  padding-right: 10px;
  min-width: 210px;
  max-width: 210px;
  display: inline-block;
  overflow-wrap: break-word;
}
.aspect-title-wrapper .required-icon {
  margin-left: 2px;
  color: red;
}
.aspect-title-wrapper .aspect-not-aligned,
.aspect-title-wrapper .single-value-warning {
  margin-left: 8px;
  color: #999;
  font-size: 16px;
}
.aspect-title-wrapper .importance-level {
  font-size: 12px;
  font-family: helvetica;
  font-weight: 300;
}
.aspect-title-wrapper .importance-level > span {
  margin-left: 2px;
}
