.ant-select-dropdown .menu-search-item {
  padding-top: 3px;
  padding-left: 5px;
  cursor: pointer;
  min-height: 24px;
}
.ant-select-dropdown .menu-search-item:hover {
  background-color: #e6f7ff;
}
.ant-select-dropdown .added-value {
  background: #FFCC0C;
}
.ant-select-dropdown .custom-not-found-text {
  color: #999;
  margin: 3px 0 3px 5px;
}
.aspect-outer-wrapper {
  margin-bottom: 10px;
  width: 40vw;
  position: relative;
}
.aspect-outer-wrapper .blank-aspect {
  height: 96px;
}
.aspect-outer-wrapper .deleted-aspect {
  background: #E7E7E7;
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.7;
  font-weight: 700;
  z-index: 80;
  padding-top: 2%;
}
.aspect-wrapper {
  display: flex;
  padding: 10px;
  border-radius: 3px;
}
.aspect-wrapper:hover {
  background: #FAFAF8;
}
.aspect-wrapper.different-value {
  background: #faf9e8;
}
.aspect-wrapper.changed-value,
.aspect-wrapper.deleted-value {
  border: 1px solid #FFCC0C;
}
.aspect-wrapper .aspect-title-wrapper {
  text-align: left;
  padding-right: 10px;
  min-width: 210px;
  max-width: 210px;
  display: inline-block;
  overflow-wrap: break-word;
}
.aspect-wrapper .aspect-title-wrapper .required-icon {
  margin-left: 2px;
  color: red;
}
.aspect-wrapper .aspect-title-wrapper .aspect-not-aligned,
.aspect-wrapper .aspect-title-wrapper .single-value-warning {
  margin-left: 8px;
  color: #999;
  font-size: 16px;
}
.aspect-wrapper .aspect-title-wrapper .importance-level {
  font-size: 12px;
  font-family: helvetica;
  font-weight: 300;
}
.aspect-wrapper .aspect-title-wrapper .importance-level > span {
  margin-left: 2px;
}
.aspect-wrapper .input-wrapper {
  width: 90%;
  display: inline-block;
}
.aspect-wrapper .input-wrapper .input-inner-wrapper {
  display: flex;
}
.aspect-wrapper .input-wrapper .ant-select {
  width: 100% !important;
}
.aspect-wrapper .input-wrapper .actions-wrapper {
  display: inline-block;
  z-index: 100;
}
.aspect-wrapper .input-wrapper .actions-wrapper button {
  margin-left: 5px;
  border: 0;
}
.aspect-wrapper .crawling,
.aspect-wrapper .original-values {
  display: flex;
  flex-wrap: wrap;
}
.aspect-wrapper .crawling .title,
.aspect-wrapper .original-values .title {
  margin-right: 5px;
}
.aspect-wrapper .crawling,
.aspect-wrapper .original-values,
.aspect-wrapper .single-value-warning {
  text-align: left;
  font-size: 12px;
  font-family: helvetica;
  font-weight: 300;
  margin-top: 3px;
}
.aspect-wrapper .crawling .crawling-suggestions,
.aspect-wrapper .original-values .crawling-suggestions,
.aspect-wrapper .single-value-warning .crawling-suggestions,
.aspect-wrapper .crawling .original-value,
.aspect-wrapper .original-values .original-value,
.aspect-wrapper .single-value-warning .original-value {
  cursor: pointer;
  margin-right: 5px;
  color: #2D69C3;
}
.aspect-wrapper .crawling .crawling-suggestions:hover,
.aspect-wrapper .original-values .crawling-suggestions:hover,
.aspect-wrapper .single-value-warning .crawling-suggestions:hover,
.aspect-wrapper .crawling .original-value:hover,
.aspect-wrapper .original-values .original-value:hover,
.aspect-wrapper .single-value-warning .original-value:hover {
  color: #214B89;
}
.aspect-wrapper .single-value-warning {
  color: red;
  font-weight: 500;
}
.aspect-wrapper .info-icon > i > svg {
  color: #999;
}
