.global-field-style,
.edit-field-wrapper {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style,
  .edit-field-wrapper {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style,
  .edit-field-wrapper {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style,
  .edit-field-wrapper {
    width: 95%;
  }
}
.edit-field-wrapper {
  margin-bottom: 10px;
}
.edit-field-wrapper .field-value {
  max-width: 800px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
}
.edit-field-wrapper:hover {
  background: #FAFAF8;
}
.edit-field-wrapper.different-value {
  background: #FCFAE2;
}
.edit-field-wrapper.changed-value {
  border: 1px solid #FFCC0C;
}
.edit-field-wrapper .label {
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  display: inline-block;
  text-align: left;
  min-width: 110px;
}
.edit-field-wrapper .button-wrapper {
  margin-left: 10px;
}
