.image-card-wrapper {
  display: inline-block;
  position: relative;
  margin-right: 36px;
}
.image-card-wrapper .image-number {
  position: absolute;
  left: 5px;
  top: 6px;
}
.image-card-wrapper .image-avatar {
  width: 141px;
  height: 141px;
  line-height: 141px;
  font-size: 18px;
}
.image-card-wrapper .image-navigation-arrow-left {
  position: absolute;
  left: -15px;
  top: 50px;
}
.image-card-wrapper .image-navigation-arrow-right {
  position: absolute;
  right: -15px;
  top: 50px;
}
.image-card-wrapper .image-footer-section {
  border: 1px solid #ddd;
  height: 24px;
}
.image-card-wrapper .image-size-details {
  position: absolute;
  left: 6px;
}
.image-card-wrapper .image-search-icon {
  position: absolute;
  right: 6px;
  line-height: 24px;
}
.image-card-wrapper .image-edit-icon {
  position: absolute;
  right: 40px;
  line-height: 24px;
}
.image-card-wrapper .image-plus-icon {
  position: absolute;
  right: 24px;
  line-height: 24px;
}
.image-card-wrapper .image-plus-icon:hover,
.image-card-wrapper .image-search-icon:hover {
  opacity: 0.7;
}
