.total-rows-done-wrapper {
  border: 1px solid #d1d1d1;
  padding: 4px 14px 1px 14px;
  display: inline-block;
  border-radius: 2px;
  font-size: 14px;
  font-family: helvetica;
  font-weight: 300;
}
.total-rows-done-wrapper .vertical-seperator {
  width: 0px;
  height: 10px;
  margin: 0 5px;
  display: inline-block;
  border-right: 1px solid #DFDFDF;
}
