.image-card-list-wrapper {
  position: relative;
  display: inline-block;
  margin-left: 20px;
  width: 100%;
  height: 550px;
  max-height: 600px;
  overflow: auto;
}
.image-card-list-wrapper .noPrimaryImage {
  width: 141px;
  height: 141px;
  border: 1px solid #ddd;
  margin-bottom: 60px;
}
.image-card-list-wrapper .product-info-wrapper {
  position: absolute;
  top: 0;
  right: 20px;
}
.image-card-list-wrapper .image-list-container {
  text-align: left;
  max-width: 720px;
}
.image-card-list-wrapper .primary-section-header,
.image-card-list-wrapper .suggested-section-header,
.image-card-list-wrapper .product-info-card-header {
  text-align: left;
  color: #595959;
  font-size: 14px;
  font-weight: 500;
}
