.global-field-style,
.input-field-wrapper {
  display: flex;
  padding: 10px;
  border: none;
}
@media only screen and (min-width: 320px) {
  .global-field-style,
  .input-field-wrapper {
    width: 35%;
  }
}
@media only screen and (min-width: 481px) {
  .global-field-style,
  .input-field-wrapper {
    width: 95%;
  }
}
@media screen and (min-width: 1619px) {
  .global-field-style,
  .input-field-wrapper {
    width: 95%;
  }
}
.input-field-wrapper {
  border-radius: 3px;
}
.input-field-wrapper:hover {
  background: #FAFAF8;
}
.input-field-wrapper.different-value {
  background: #FCFAE2;
}
.input-field-wrapper.changed-value {
  border: 1px solid #FFCC0C;
}
.input-field-wrapper .label {
  display: inline-block;
  text-align: left;
  min-width: 110px;
}
